    import appNoGoods from '../../../components/page-component/app-no-goods/app-no-goods.vue';
    import {mapGetters, mapState} from "vuex";

    export default {
        name: "index",
        data() {
            return {
                list: [],
                background: '',
                form: {
                    bg: {
                        color: ''
                    },
                    text: {
                        color: ''
                    }
                },
                activity: {
                    rule_price: 0,
                    rule_num: 0
                },
                page: 1,
                page_count: 1,
                storage: [],
                loading: false,
				disable: 'disable'
            }
        },
        components: {
            appNoGoods
        },
        methods: {
            getList() {
                this.$showLoading();
                this.loading = false;
                this.$request({
                    url: this.$api.pick.goods_list,
                    method: 'get'
                }).then(e => {
                    if (e.code === 0) {
                        this.$hideLoading();
                        let { bg_url, list, form, activity, pagination } = e.data;
                        this.background = bg_url;
                        this.form = form;
                        this.activity = activity;
                        this.list = list;
                        this.loading = true;
                        this.page_count = pagination.page_count;
                    } else if (e.code === 1) {
                        uni.showModal({
                            title: '提示',
                            content: e.msg,
                            success: function() {
                                uni.navigateBack();
                            }
                        });
                        this.$hideLoading();
                    }
                }).catch(() => {
                    this.$hideLoading();
                });
            },

            navigator(str) {
                uni.navigateTo({
                    url: str
                });
            },
            goRoute() {
                uni.navigateTo({
                    url: `/pages/rules/index?url=${encodeURIComponent(this.$api.pick.goods_list)}&key=rule`,
                });
            },


            async getBottom() {
                const e = await this.$request({
                    url: this.$api.pick.goods_list,
                    method: 'get',
                    data: {
                        page: this.page
                    }
                });
                if (e.code === 0) {
                    this.list.push(...e.data.list);
                }
            },

            async getCart() {
                const e = await this.$request({
                    url: this.$api.pick.list
                });
                if (e.code === 0) {
                    let data = e.data.list;
                    this.storage = 0;
                    for (let i = 0; i < data.length;i++) {
                        this.storage = Number(this.storage) +  Number(data[i].num);
                    }
                }
            }
        },
        onLoad() { this.$commonLoad.onload();

            wx.showShareMenu({
                menus: ['shareAppMessage', 'shareTimeline']
            })

            this.getList();
        },
        onReachBottom() {
            if (this.page_count > this.page) {
                this.page++;
                this.getBottom();
            }
        },
        computed: {
            ...mapState({
                isShowSalesNum: state => state.mallConfig.mall.setting.is_show_sales_num,
                appImg: state => state.mallConfig.__wxapp_img.mall,
                appSetting: state => state.mallConfig.mall.setting,
            }),
            ...mapGetters('mallConfig', {
                getTheme: 'getTheme',
            })
        },

        onShareTimeline() {
            // 分享朋友圈beta
            return this.$shareTimeline({
                title: this.$children[0].navigationBarTitle,
                query: {
                } // 此处填写页面的参数
            });
        },

        filters: {
            getStorage(data) {
                if (data > 99) {
                    return '99+';
                } else {
                    return data;
                }
            }
        },
        onShow() {
            this.getCart();
        },
        onShareAppMessage() {
            return this.$shareAppMessage({
                path: '/plugins/pick/index/index',
                title: this.$children[0].navigationBarTitle,
            });
        }
    }
